@use '../../styles' as *;

.container {
  background-color: hsla(205, 6%, 17%, 1); // $BACKGROUND_DARK
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  outline: none;
  user-select: none;
  cursor: grab;
  overflow: hidden;

  &.grabbing {
    cursor: grabbing;
  }

  .disable {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background: rgba(255, 255, 255, 0.3);
  }

  .tools {
    z-index: 2;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: em(16);
    gap: em(8);
  }
}

.title {
  position: absolute;
  font-weight: 700;
  font-size: em(16);
  line-height: em(18.56);
  letter-spacing: em(.2);
  color: #404244;
  top: em(16);
  left: em(16);
}

