@use '../../styles' as *;

.articleItem {
  @extend %column;

  gap: rem(4);
}

.header {
  @extend %row;
  @extend %caption-small;

  gap: get-grid-space(1x);
}

.date {
  color: $txt-primary;
}

.fieldOfStudy {
  color: $txt-secondary;

  &::first-letter {
    text-transform: capitalize;
  }

  &::after {
    content: ',',
  }

  &:last-child {
    &::after {
      content: '',
    }
  }
}

.title {
  @extend %body-large-accent;

  cursor: pointer;

  &::first-letter {
    text-transform: capitalize;
  }
}

.footer {
  @extend %row;

  gap: get-grid-space(1x);
}

.citeCount {
  @extend %row;
  @extend %caption-small-accent;

  flex: 0;
  color: $txt-primary;
}

.authors {
  @extend %row;
  @extend %caption-small;

  color: $txt-secondary;
  gap: get-grid-space(1x);
}

.author {
  &::first-letter {
    text-transform: capitalize;
  }
}

.authorsCount {
  @extend %px-halfX;

  border-radius: rem(2);
  background-color: $bg-secondary;
}
